import React from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import './App.css';
import Layout from './components/Layout'
import GuildPicker from './components/GuildPicker'
import GuildCompare from './components/GuildCompare'
import SkillPicker from './components/SkillPicker'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import themeOverrides from './theme.js';
const theme = createMuiTheme(themeOverrides);

function App() {
  return (
    <MuiThemeProvider theme={theme}>
      <Router basename={process.env.PUBLIC_URL}>
        <Layout>
          <Route path={`${process.env.PUBLIC_URL}/`} exact component={GuildPicker} />
          <Route path={`${process.env.PUBLIC_URL}/guild-picker/`} component={GuildPicker} />
          <Route path={`${process.env.PUBLIC_URL}/guild-compare/`} component={GuildCompare} />
          <Route path={`${process.env.PUBLIC_URL}/skill-picker/`} component={SkillPicker} />
        </Layout>
      </Router>
    </MuiThemeProvider>
  );
}

export default App;
